#tech-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: white;
}

#tech-title {
    font-size: 2em;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

#tech-content-container {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.tech-content {
    background-color: #150050;
    width: 50%;
    height: auto;
    border: 3px solid #610094;
    box-shadow: 10px 10px 16px #3F0071;
    border-radius: 10px;
    margin: 15px;
    padding: 10px;
}

.tech-content-content ul {
    list-style: none;
}

.tech-content-content ul li {
    font-size: 1.2em;
}

.left {
    margin-left: auto;
}

.tech-content-content ul li::before {
    content: "•"; 
    color: white;
    font-weight: bold;
    display: inline-block;
    width: 1em;
}

@media (max-width: 768px) {
    .tech-content {
        width: 95%;
        margin: 15px;
    }

    #tech-title h1 {
        font-size: 1.5em;
    }

    #tech-content-container {
        align-items: center;
        justify-content: center;
    }

    .left {
        margin-left: none;
    }
}

@media (max-width: 510px) {
    #tech-title {
        font-size: 1.3em;
    }
}

@media (max-width: 440px) {
    #tech-title {
        font-size: 1.2em;
    }
}

@media (max-width: 360px) {
    #tech-title {
        font-size: 1em;
    }
}

@media (max-width: 320px) {
    #tech-title {
        font-size: 1.1em;
    }
}

@media (max-width: 280px) {
    #tech-title {
        font-size: 1em;
    }
}

