#hero-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#hero-wrapper {
    height: 90%;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #150050;
    border: 3px solid #610094;
    box-shadow: 10px 10px 16px #3F0071;
    color: white;
    border-radius: 10px;
}

#hero-wrapper h1 {
    margin: 1px;
    padding: 0;
}

#hero-typer {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin: 1rem;
}

#hero-typer * {
    margin: 0;
    padding: 0;
}

#hero-typer-text {
    font-size: 2em;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

#hero-image {
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    pointer-events: none;
}

#hero-wrapper p {
    font-size: 1.5em;
    text-align: center;
}

#hero-image img {
    width: 150px;
    height: 217.63px;
}

@media (max-width: 768px) {
    #hero-wrapper {
        width: 95%;
        height: 95%;
    }

    #hero-typer-text {
        font-size: 1.5em;
    }

    #hero-typer p {
        font-size: 1.3em;
    }

    #hero-image img {
        width: 100px;
        height: 145.09px;
    }

    #hero-wrapper h1 {
        font-size: 1.7em;
    }
}