#contact-container {
    width: 100%;
    height: 100%;
    background-color: #1F1D36;
}

#contact-content {
    margin: 15px;
}

#contact-title {
    font-size: 1.5em;
    font-weight: bold;
    color: white;
    text-align: center;
    margin: 0;
}

#contact-socials {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    margin: 0;
    padding: 0;
}

#contact-socials * {
    color: white;
    margin: 5px;
    font-size: 2em;
    margin-top: 0;
}

#contact-content h2 {
    font-size: 1.5em;
    font-weight: bold;
    color: white;
    text-align: center;
    margin: 0;
}

#contact-content a {
    color: white;
}