#about-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

#about-wrapper {
    height: 90%;
    width: 90%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    background-color: #150050;
    border: 3px solid #610094;
    box-shadow: 10px 10px 16px #3F0071;
    color: white;
    border-radius: 10px;
}

#about-title {
    font-size: 1.7em;
    font-weight: bold;
    margin: 0;
    padding: 0;
}

#about-content {
    font-size: 1.2em;
    text-align: center;
    margin: 0 15px 0 15px;
}

@media (max-width: 880px) {
    #about-title {
        font-size: 1.5em;
    }

    #about-content {
        font-size: 1em;
    }
}

@media (max-width: 768px) {
    #about-title {
        font-size: 1.3em;
    }

    #about-content {
        font-size: 0.8em;
    }

    #about-wrapper {
        width: 95%;
        height: 95%;
    }
}